<template>
  <div class="box">

    <SongHead :songs="songs" :isList="isList"  :isDetails="isDetails"/>
    <div class="info">
      <Carte :songs="songs"/>
    </div>
<!--    <p></p>-->
    <div class="List" >
      <ul>
        <SongItem v-for="( songs ,index) in  songs.tracks" :isPlaying="isPlaying" :playSong="playSong" :index="index" :songs="songs" :key="index" @begin="begin($event)" />
      </ul>
    </div>
  </div>
</template>

<script>
import SongHead from '../components/SongHead'
import SongItem from '../components/SongItem'
import Carte from '../components/Carte'

export default {
  name: "SongList",
  components:{
    SongHead,
    SongItem,
    Carte
},
  props:{
    playSong:Object,
    isPlaying:Boolean,
    isList:Boolean,
    isDetails:Boolean
  },
  created() {
    this.getDate(this.$route.query.id)
  },
  data (){
    return{
      songs:null
    }
  },
  methods:{
    getDate(id){
      this.axios
          .get('https://apis.netstart.cn/music/playlist/detail?id='+id)
          .then((res)=>{
            this.songs = res.data.playlist
            this.$emit('update:songs',this.songs)
          })
    },
      begin(songs){
        this.$emit('begin',songs)
      }
  }
}
</script>

<style lang="less" scoped>

  .box{
      position: relative;
    background-color: white;
      //display: flex;
      //flex-direction: column;
      //align-items: center;

    .info{
      //left: calc(100%);
      position: absolute;
      height: 35px;
      background-color: white;
      width: 70%;
      top: 180px;
      left: 15%;
      border-radius: 20px;
      box-shadow: 1px 1px 1px -1px slategray;

    }
    p{
      font-size: 12px;
      margin-top: 10px;
      background-color: #cccccc;
    }
    .List{
      margin-top: 20px;
    }
  }
</style>