<template>

  <div class="bar" :style="{bottom:status?0:'-50px'}">
    <div class="left" >
      <div class="songPic" :class="{pause:!isPlaying}">
        <img
            :src="playSong !== null ?  (playSong.picUrl !== undefined ?  playSong.picUrl :  playSong.al.picUrl):  picUrl"
            alt="">
      </div>
      <div class="info">
        <h1 class="name">{{
            playSong !== null ? playSong.name !== undefined ? playSong.name : playSong.al.name : name
          }}</h1>
        <span class="singer">
           <span v-if="!status">{{ singer }}</span>
           <span v-else
                 v-for="(s,index) in (playSong.song !== undefined ? playSong.song.artists :  playSong.ar )  "
                 :key="index"
           >
            {{ s.name }}
          </span>
        </span>

      </div>
    </div>
    <div class="right" >
      <span @click.stop="$emit('playing')">
        <img v-if="!isPlaying" class="play" src="../assets/czf.png" alt="">
        <img v-else class="pause" src="../assets/cze.png" alt="">
        <canvas
            ref="canvas"
            width="36"
            height="36"
            style="display: block; width: 30px; height: 30px "
        ></canvas>
      </span>
      <svg @click.stop="$parent.isList = true" class="list" data-v-5f9336bf="" t="1635921750579" viewBox="0 0 1024 1024" version="1.1"
           xmlns="http://www.w3.org/2000/svg" p-id="5481" width="20" height="20">
        <path data-v-5f9336bf=""
              d="M469.333333 149.333333a21.333333 21.333333 0 0 1 21.333334-21.333333h469.333333a21.333333 21.333333 0 0 1 0 42.666667H490.666667a21.333333 21.333333 0 0 1-21.333334-21.333334z m490.666667 362.666667H64a21.333333 21.333333 0 0 0 0 42.666667h896a21.333333 21.333333 0 0 0 0-42.666667z m0 384H64a21.333333 21.333333 0 0 0 0 42.666667h896a21.333333 21.333333 0 0 0 0-42.666667zM42.666667 384c0-23.626667 12.22-46.34 33.526666-62.32C96 306.84 121.953333 298.666667 149.333333 298.666667c23.333333 0 45.666667 5.946667 64 16.893333V64a21.333333 21.333333 0 0 1 42.666667-1.113333c0.74 14.14 3.52 35.48 12.553333 56.78 5.833333 13.753333 13.546667 25.613333 22.36 34.42a107.46 107.46 0 0 1 20.493334 18.78c12.6 14.853333 22.373333 33.566667 29.04 55.626666a21.333333 21.333333 0 0 1-40.84 12.346667C294.666667 224.666667 287.513333 210.666667 278.846667 200.466667a64.193333 64.193333 0 0 0-13.3-12 21.133333 21.133333 0 0 1-2.96-2.366667q-3.38-3.233333-6.586667-6.813333V384c0 23.626667-12.22 46.34-33.526667 62.32C202.666667 461.16 176.713333 469.333333 149.333333 469.333333s-53.333333-8.173333-73.14-23.013333C54.886667 430.34 42.666667 407.626667 42.666667 384z m42.666666 0c0 23.126667 29.333333 42.666667 64 42.666667s64-19.54 64-42.666667-29.333333-42.666667-64-42.666667-64 19.54-64 42.666667z"
              fill="#5C5C66" p-id="5482"></path>
      </svg>
    </div>

  </div>

</template>

<script>

export default {
  name: "Bar",
  components:{

  },
  props: {
    status: Boolean,
    playSong: Object,
    isPlaying: Boolean,
    currentTime:Number,
    duration:Number
  },
  data() {
    return {
      picUrl: 'https://p1.music.126.net/oZYWmV_y-7z_4y_pfap_TQ==/109951166423703455.jpg',
      singer: 'Taylor Swift',
      name: 'Wildest Dreams (Taylor\'s Version)',
    }
  },
  watch: {
    currentTime: function (n) {
      n;
      var context = this.$refs.canvas.getContext("2d");
      context.lineWidth = 2;
      context.clearRect(0, 0, 36, 36);
      context.beginPath();
      context.arc(
          18,
          18,
          17,
          (Math.PI / 180) * 360 * (this.currentTime / this.duration) - 90,
          (Math.PI / 180) * 360
      );
      context.strokeStyle = "#ccc";
      context.stroke();
      context.closePath();

      context.beginPath();
      context.arc(

          18,
          18,
          17,
          (Math.PI / 180) * 0 - 90,
          (Math.PI / 180) * 360 * (this.currentTime / this.duration) - 90
      );
      context.strokeStyle = "red";
      context.stroke();
    },
  },
  methods: {}
}
</script>

<style lang="less" scoped>
@keyframes loading {
  from {
    transform: rotate(-360deg);
  }
  to {
    transform: rotate(0deg);
  }
}

.bar {

  display: flex;
  justify-content: space-between;
  position: fixed;
  //bottom: -50px;
  height: 50px;
  background-color: white;
  width: 100%;
  //z-index: 10;
  transition: bottom 1s;

  .left {
    display: flex;
    flex-wrap: nowrap;
    width: 50%;

    .songPic {

      img {
        margin: 3px;
        animation-play-state: running;
        background-size: cover;
        width: 45px;
        height: 45px;
        max-width: none;
        border-radius: 50%;
        animation: loading 10s linear infinite;

      }
      &.pause{
        img{
          animation-play-state: paused;
        }
    }

    }

    .info {
      padding-left: 5px;
      width: calc(100% - 25px);
      display: flex;
      flex-direction: column;
      justify-content: center;

      .name {
        width: calc(100% - 25px);
        font-size: 16px;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
      }

      .singer {
        width: calc(100% - 25px);
        font-size: 12px;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
        color: #8e8e8e;

        span {
          &:after {
            margin: 0 5px;
            content: "/";
          }

          &:last-of-type {
            &::after {
              content: "";
            }
          }
        }
      }
    }

  }

  .right {
    display: flex;
    align-items: center;

    span{
      margin-top: 2px;
      position: relative;
      width: 22px;
      height: 22px;
      .play{
        margin-left: 2px;
      }
      .play, .pause {
        //margin-top: 4px;
        width: 20px;
        height: 20px;
      }
      canvas{
        position: absolute;
        //background-color: sandybrown;
        opacity: 0.4;
        z-index: -1;
        top: -5px;
        left: -5px;
      }
    }


    .list {
      margin: 0 15px;
      width: 25px;
      height: 25px;
    }
  }
}
</style>