<template>
  <div class="box">
    <div class="center">
      <img class="pointer" :class="{playing:isPlaying,pause:!isPlaying }" src="../assets/cdo.png" alt="">
      <img class="pic" :class="{pause:!isPlaying}" :src=" playSong.picUrl ||  playSong.al.picUrl " alt="">
      <img class="bd" :class="{pause:!isPlaying}" src="../assets/eq7.png" alt="">
    </div>
  </div>
</template>

<script>
export default {
  name: "PagePic",
  props:{
    playSong:Object,
    isPlaying:Boolean,
}
}
</script>

<style lang="less" scoped>
@keyframes loading {
  from {
    transform: rotate(-360deg);
  }
  to {
    transform: rotate(0deg);
  }
}
  .box{
    .center{
      display: flex;
      justify-content: center;
      align-items: center;
      position: relative;
      height: 50vh;
      margin-top: 30vw;
      .pointer{
        top: -20vw;
        left: 45vw;
        width: 25vw;
        position: absolute;
        transform-origin: 2vw 2vw ;
        transition: transform 1s;
        &.playing{
          transform:rotate(-6deg);
        }
        &.pause{
          transform:rotate(-25deg);
        }
      }

      .pic{
        animation-play-state: running;
        border-radius: 50vw;
        width: 40vw;
        height: 40vw;
        position: absolute;
        animation: loading 10s linear infinite;
        &.pause{
          animation-play-state: paused;
        }
      }
      .bd{
        width: 75vw;
        height: 75vw;
        animation: loading 10s linear infinite;
        z-index: -1;
        &.pause{
          animation-play-state: paused;
        }
      }
    }
  }
</style>