<template>
  <li >
    <div class="item" @click="$emit('begin',index)">
      <div class="song">
        <p class="songName" >{{songs.name}}</p>
        <div class="info">
          <span>
            <span class="artist"
                  v-for="(artist,index) in (songs.song?songs.song.artists:songs.ar) "
                  :key="index">
            {{ artist.name }}</span>
          </span> -&nbsp;
          <span class="artist" > {{(songs.song?songs.song.album.name:songs.al.name)}}</span>
        </div>
      </div>
      <div class="play">
          <span class="icon"  v-show="( playSong? playSong.id : null) !== songs.id">
           <svg t="1635790675413"  viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="2430" width="200" height="200"><path d="M512 0C230.4 0 0 230.4 0 512s230.4 512 512 512 512-230.4 512-512S793.6 0 512 0z m0 981.333c-258.133 0-469.333-211.2-469.333-469.333S253.867 42.667 512 42.667 981.333 253.867 981.333 512 770.133 981.333 512 981.333z" p-id="2431" fill="#aaaaaa"></path><path d="M672 441.6L501.333 328.533c-57.6-38.4-106.666-12.8-106.666 57.6v256c0 70.4 46.933 96 106.666 57.6L672 586.667C729.6 544 729.6 480 672 441.6z" p-id="2432" data-spm-anchor-id="a313x.7781069.0.i2" fill="#aaaaaa" class="selected"></path></svg>
          </span>
        <div class="donghua" :class="{pause:!isPlaying}" v-show="( playSong? playSong.id : null) === songs.id">
          <span></span>
          <span></span>
          <span></span>
        </div>
      </div>
    </div>
  </li>

</template>


<script>
export default {
  name: "Item",
  props:{
    songs:Object,
    isPlaying:Boolean,
    playSong:Object,
    index:Number,
  }
}
</script>

<style lang="less" scoped>
@import "../assets/var";
@keyframes loading {
  from{
    transform:scaleY(0.2) ;
  }
  to{
    transform:scaleY(1) ;
  }
}

li{
  padding:  6px 0 ;
  margin-left: 10px;
  border-bottom: 1px solid rgba(204, 204, 204, .3)
;
  .item{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-right: 10px;
    .song{
      flex: 1;
      display: inline-block;
      .songName{
        font-size: 18px;
        line-height: 26px;
        display: -webkit-box;
        -webkit-line-clamp: 1;
        -webkit-box-orient: vertical;
        overflow: hidden;
      }
      .info{
        width: 70%;
        font-size: 14px;
        display: -webkit-box;
        -webkit-line-clamp: 1;
        -webkit-box-orient: vertical;
        overflow: hidden;
        color: #8e8e8e;
        .artist{
          font-size: 12px;
          &::after {
            margin: 0 5px;
            content: "/";
          }
          &:last-of-type {
            &::after {
              content: "";
            }
          }
        }
      }
    }
    .play{
      width: 25px;
      height: 25px;
      margin-right: 15px;
      //background: red;
      display: flex;
      justify-content: center;
      align-items: center;
      svg{
        width: 100%;
        height: auto;
      }
      .donghua{
        display: flex;
        height: 60%;
        width: 60%;
        justify-content: space-between;
        align-items: center;
        span{
          animation-play-state: running;
          animation: loading 0.9s linear infinite alternate;
          width: 20%;
          height: 100%;
          display: block;
          background: @red;
          //animation-play-state: paused;
          transform-origin: bottom;
          &:nth-child(1) {
            animation-delay: -0.6s;
          }

          &:nth-child(2) {
            animation-delay: -0.3s;
          }
        }
        &.pause {
          span{
            animation-play-state: paused;

          }
        }
      }
    }
  }
}
</style>