<template>
  <div class="home">
    <div class="home-title">
      
    </div>
    <ul class="recommend">
      <SongCard  v-for="song in info"  :songs="song"  :key="song.id"/>
    </ul>
    <div class="home-title">
      
    </div>
    <ul class="newSong">
      <NewList v-for="(song,index) in newSongs" :index="index" @update:songs="$emit('update:songs',$event)" :newSongs="newSongs" :isPlaying="isPlaying" :playSong="playSong" :songs="song" @click.native="$emit('us',newSongs)" :key="song.id" :status="status"  @begin="$emit('begin',$event)"/>
    </ul>
  </div>
</template>

<script>
import SongCard from '../components/SongCard.vue'
import NewList from '../components/NewList'

// @ is an alias to /src
export default {
  name: 'Home',
  components: {
    SongCard,
    NewList,
  },
  props: {
    status: Boolean,
    playSong: Object,
    isPlaying: Boolean
  },
  data() {
    return {
      info: null,
      newSongs: null
    }
  },
  created() {
    this.axios.get('https://apis.netstart.cn/music/personalized?limit=6').then((res) => {
      this.info = res.data.result
      // console.log(res.data.result)
    })
    this.axios
        .get("https://apis.netstart.cn/music/personalized/newsong")
        .then((res) => {
          this.newSongs = res.data.result;
          // console.log(res.data.result)
        });
  },
}

</script>
