<template>
  <div class="card" @click="enter(item.id)">
    <div class="update">{{item.updateFrequency}}</div>
    <div class="info">
      <div class="title">
        <h1>{{item.name}}</h1>
        <img
            v-for="o in cardList"
            :key="o.id"
            alt=""
            :src="o.al.picUrl + '?imageView=1&type=webp&thumbnail=246x0'"
        />

      </div>
    </div>
    <ul class="songList">
      <li v-for="(song, index) in cardList" :key="song.id">
        <span class="song">{{ index + 1 }}.{{ song.name }}</span>
        <span class="singer" v-for="(singer,o ) in song.ar" :key="o"> -{{ singer.name }}</span>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: "RankCard",
  props:{
    item:Object
  },
  data(){
    return{
      cardList:{}
    }
  },
  created: function () {
    this.axios
        .get("https://apis.netstart.cn/music/playlist/detail?id=" + this.item.id)
        .then((res) => {
          this.cardList = res.data.playlist.tracks.slice(0,3);
        });
  },
  methods:{
    enter(id){
      console.log(123)
      this.$router.push({
        path:'/songlist',
        query:{
          id
        }
      })
    }
  }
}
</script>

<style lang="less" scoped>
.card{

  position: relative;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  //height: 300px;
  background: #4568DC;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #B06AB3, #4568DC);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #B06AB3, #4568DC); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  z-index: 0;

  border-radius: 15px;
  margin: 15px 15px 5px;
  padding: 5px 15px 5px 0;
  .update{
    position: absolute;
    right: 15px;
    color: rgba(204, 204, 204, 0.92);
    font-size: 12px;
    text-align: right;
  }
  .info {
    width: 100px;
    margin: 15px 15px;

    text-align: center;
.title{
  position: relative;
  z-index: 1;
    h1 {
      font-size: 30px;
      margin-bottom: 5px;
      color: rgba(235, 242, 241, 0.87);
    }

    img {
      width: 80px;
      display: block;
      // display: block;
      vertical-align: middle;
      border-radius: 10px;
      bottom: 0;


      &:first-child {
        position: absolute;
        right: 20px;
        width: 80px;
        bottom: 20px;
        z-index: 99999;

      }

      &:nth-child(2) {
        position: absolute;
        width: 50px;
        right: 10px;
        z-index: -1;
        opacity: 0.8;
        filter: grayscale(0.8) brightness(1.1);
      }

      &:nth-child(3) {
        width: 40px;
        position: absolute;
        right: 0;
        z-index:-2;
        filter: grayscale(0.6) brightness(1.2);
      }
    }
}
  }
  .songList{
    font-size: 14px;
    margin:  15px 15px;
    flex: 1 ;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    li{
      color: #ebf2f1;
      display: -webkit-box;
      -webkit-line-clamp: 1;
      -webkit-box-orient: vertical;
      overflow: hidden;
    }
  }
}
</style>