<template>
  <div id="app">
    <div id="nav"  v-if="$route.path !== '/songlist' ">
      <li><router-link to="/"></router-link></li>
      <li><router-link to="/rank"></router-link></li>
      <li><router-link to="/search"></router-link></li>
    </div>
    <router-view
        @ss="ups"
        @update:songs="updateSongs($event)"
        :status="status"
        :playSong="playSong"
        @begin="begin"
        :isPlaying="isPlaying"
        :isList="isList"
        :isDetails="isDetails"
        @us="dangegdan"

    />
    <audio @play="isPlaying = true" @pause="isPlaying = false" @durationchange="onDurationchange" ref="Playing" @timeupdate="onTimeupdate" @ended="end" v-if="playSong"  :src="playSongUrl" controls autoplay ></audio>
    <transition name="fade">
    <PlayPage @jia="jia" @jian="jian" :Songs="Songs" :duration="duration" @update:time="updateTime($event)" :currentTime="currentTime" v-if="isDetails" :playSong="playSong" :isPlaying="isPlaying" @playing="playing" @pause="pause"/>
    <Bar   v-else :duration="duration" :currentTime="currentTime" @click.native="isDetails=true" :status="status"  :playSong="playSong"  :isPlaying="isPlaying" @playing="playing"  @pause="pause"/><SongList />
    </transition>
    <section class="boList" v-show="isList" @click="isList = false">
      <transition name="fade">
        <div v-if="isList" class="mask"></div>
      </transition>
      <transition name="slide">
        <div class="content" v-if="isList">
          <ul>
      <Item  v-for="( songs ,index) in  Songs" :playSong="playSong" :isPlaying="isPlaying" :index="index" :songs="songs" :key="index" @begin="begin($event)" />
          </ul>
        </div>
      </transition>
    </section>
  </div>
</template>

<script>
import Bar from './components/Bar'
import PlayPage from './components/PlayPage'
import Item from './components/Item'

export default {
  components: {
    Bar,
    PlayPage,
    Item
  },
  data(){
    return{
      status:false,
      playSong:null,
      isPlaying:true,
      isDetails:false,
      currentTime:0,
      duration:0,
      isList:false,
      Songs:null,
      m:9999999
    }
  },
  computed:{
    playSongUrl(){
      if(!this.playSong) return ''
      return `https://music.163.com/song/media/outer/url?id=${this.playSong.id}.mp3`;
    }
  },
  methods:{
    ups(event){
      console.log(event)
      this.status = true

      this.playSong = event
      this.currentTime= 0
    },
    jia(){
      this.m++
      if(this.Songs.length-1 === this.m){
        this.m = 0
      }
    },
    jian(){
      this.m--
      if(0 === this.m){
        this.m = this.Songs.length-1
      }
    },
    dangegdan(event){
      this.Songs = event
    },
    end(){
      this.m++
      if(this.Songs.length-1 === this.m){
        this.m = 0
      }
    },
    updateSongs(songs){

      if(songs.tracks){
        this.Songs = songs.tracks
      }else {
        this.Songs = songs
      }

    },
    updateTime(t){
      console.log(t)
      this.$refs.Playing.currentTime = t
    },
    playing() {
      if (this.isPlaying) {
        this.$refs.Playing.pause()
      } else {
        this.$refs.Playing.play()
      }
    },
    begin(index) {
      // this.playSong=this.Songs[index]
      this.status = true
      this.m = index
      // this.playSong = songs
      this.currentTime= 0
    },
    onTimeupdate(){
      // console.log(this.$refs.Playing.currentTime)
      this.currentTime =this.$refs.Playing.currentTime
    },
    onDurationchange(){
      this.duration = this.$refs.Playing.duration
    }
  },
  watch:{
    currentTime(){
      this.isPlaying = true
    },
    m(n){
      this.playSong = this.Songs[n]
    }
  }
}
</script>
