<template>
  <div class="rank">
    <div class="globalRank">
      <div class="title">
        
      </div>

      <ul>
        <GlobalRank :item="item" v-for="item in globals" :key="item.id" />
      </ul>
    </div>

    <div class="officialRank">
      <div class="title">
        <img src="//s1.music.126.net/style/favicon.ico?v20180307" alt="">
      </div>
        <RankCard :item="item" v-for="item in rankCard" :key="item.id" @click="enter(item.id)"  />
    </div>
  </div>
</template>

<script>
import GlobalRank from '../components/GlobalRank'
import RankCard from '../components/RankCard'
export default {
  name: "Rank",
  components:{
    GlobalRank,
    RankCard
  },
  data(){
    return{
      ranks:{},
      globals:[],
      rankCard:[]
    }
  },
  created() {
    this.axios
      .get('https://apis.netstart.cn/music/toplist')
      .then((res)=> { this.ranks = res.data.list
        // console.log(res.data.list)
        this.globals.push(res.data.list[11],res.data.list[12],res.data.list[15],res.data.list[18],res.data.list[33],)
        this.rankCard.push(res.data.list[0],res.data.list[1],res.data.list[3],res.data.list[2])
      })
  }

}
</script>

<style lang="less" scoped>
  .title{
    margin: 6px;
    font-size: 20px;
    font-weight: bold;
  }
  .rank{
    //z-index: -9999;
    display: flex;
    flex-direction: column;
    .globalRank{
      margin: 5px 15px;
      border-radius: 10px;
      background-color: white;
      padding: 10px;
      ul{
        display: flex;
        overflow: hidden;

      }
    }
    .officialRank{
      //z-index: -4;
      margin: 0 15px;
      .title{
        display: flex;
        align-content: center;
        height: 20px;
        img{
          margin-top: 4px;
          margin-right: 5px;
          width: 20px;
          height: 20px;
        }
      }
    }
  }
</style>