<template>
  <li @click="enter(songs.id)">
    <span class="ting">{{songs.playCount>10000?((songs.playCount)/10000).toFixed(1)+'':songs.playCount}}</span>
    <img :src="songs.picUrl+'?imageView=1&type=webp&thumbnail=246x0'"  alt="">
    <span>{{songs.name}}</span>
  </li>
</template>

<script>
export default {
  name: "SongCard",
  props:{
    songs:Object
  },
  data(){
    return{

    }
  },
  methods:{
    enter(id){
      this.$router.push({
        path:'/songlist',
        query:{
          id
        }
      })
    }
  }
}
</script>

<style lang="less" scoped>
  li{
    width: 33vw;
    display: flex;
    flex-direction: column;
    position: relative;
    .ting{
      background-color: rgba(177,177,177,0.6);
      border-radius: 6px;
      position: absolute;
      font-size: 12px;
      right: 2px;
      top: -5px;
      color: #3f3f3f;
    }
    span{
      display: -webkit-box;
      -webkit-line-clamp: 2;
      -webkit-box-orient: vertical;
      overflow: hidden;
      box-sizing: border-box;
      padding:  0 3px ;
      font-size: 13px;
      margin: 8px 0 ;
      //line-height: 12px;
    }
  }
</style>