<template>
  <li  @click="enter(item.id)" >
    <div class="img" >
      <img :src=item.coverImgUrl alt="">
      <span class="update">{{item.updateFrequency}}</span>
      <div class="playIcon"></div>
    </div>
    <h1>{{ item.name }}</h1>
  </li>

</template>
<script>
export default {
  name: "GlobalRank",
  props:{
    item:Object
  },
  methods:{
    enter(id){
      this.$router.push({
        path:'/songlist',
        query:{
          id
        }
      })
    }
  }
}
</script>

<style lang="less" scoped>
  li{
    width: calc((100% - 30px)/3);
    flex-shrink: 0;
    margin: 0 5px;
    .img{
      position: relative;
      border-radius: 15px;
      overflow: hidden;
      img{
        display: block;
        width: 100%;
        height: 100%;
      }
      .update{
        font-size: 12px;
        background-color: rgba(255,255,255,0.3);
        position: absolute;
        padding: 1px 2px;
        top: 5px;
        right: 5px;
        border-radius: 25px;
        color: #efefef;
      }
    }
    h1{
      font-size: 13px;
      padding-left: 3px;
      display: -webkit-box;
      -webkit-line-clamp: 1;
      -webkit-box-orient: vertical;
      overflow: hidden;
    }
  }
</style>