<template>
  <keepAlive>
    <div class="search-page">
      <div class="container">
        <div class="input-box">
          <input
            type="text"
            @keydown.enter="search"
            :placeholder="defultWord"
            v-model="searchKeyWord"
            @focus="inSeach = true"
            @blur="inSeach = false"
          />
          <div
            class="clear-search"
            v-if="!showHotKeyWord"
            @click="searchKeyWord = ''"
          >
            X
          </div>
        </div>

        <!--  -->
        <div v-if="showHotKeyWord" class="hot-search-list">
          <span
            v-for="(item, index) in hotSearchs"
            :key="'hot' + index"
            @click="searchHot(item.first)"
            >{{ item.first }}</span
          >
        </div>
        <!--  -->
        <ul class="search-history" v-if="showHotKeyWord">
          <li
            v-for="(history, index) in historySearchs"
            :key="'history' + index"
            @click="searchHot(history)"
          >
            <p>{{ history }}<span @click.stop="dleHistory(history)">X</span></p>
          </li>
        </ul>
        <!--  -->
        <ul class="search-suggest" v-if="inSeach && !showHotKeyWord">
          <h3
            v-if="searchKeyWord.trim()"
            class="key-word-search"
            @mousedown="search"
          >
            {{ searchKeyWord.trim() }}
          </h3>
          <li
            v-for="(suggest, index) in suggests"
            :key="'suggest' + index"
            @mousedown="searchHot(suggest.keyword)"
          >
            <p>{{ suggest.keyword }}</p>
          </li>
        </ul>
<!--        <p v-if="!inSeach"></p>-->
        <ul v-if="!inSeach">
          <Item
              :isPlaying="isPlaying"
              @begin="$emit('ss',songs)"
            v-for="songs in searchSongs"
            :key="songs.id"
            :songs="songs"
            :isPlayerMusicId="isPlayerMusicId"
            :playing="playing"
            :playSong="playSong"
          />
        </ul>
      </div>
    </div>
  </keepAlive>
</template>

<script>
import Item from '../components/Item'
export default {
  components: {Item},
  props: {
    isPlayerMusicId: Number,
    playing: Boolean,
    isPlaying:Boolean,
    playSong:Object
  },
  data() {
    return {
      defultWord: "",
      hotSearchs: [],
      searchKeyWord: "",
      realkeyword: "",
      unsearch: true,
      searchSongs: [],
      showHotKeyWord: true,
      inSeach: false,
      suggests: [],
      historySearchs: [],
    };
  },
  methods: {
    dleHistory(history) {
      this.historySearchs = this.historySearchs.filter((o) => o != history);
    },
    search() {
      this.unsearch = false;
      this.axios
        .get(
          "https://apis.netstart.cn/music/cloudsearch?keywords=" +
            this.searchKeyWord.trim()
        )
        .then((res) => {
          this.searchSongs = res.data.result.songs;
        });
      this.inSeach = false;
      if (this.historySearchs.every((o) => o != this.searchKeyWord))
        this.historySearchs.push(this.searchKeyWord);
    },
    searchHot(keyword) {
      this.searchKeyWord = keyword;
      this.search();
    },
  },
  watch: {
    historySearchs(n) {
      window.localStorage.setItem("serach-history", JSON.stringify(n));
    },
    searchKeyWord(n) {
      if (n.trim()) {
        this.showHotKeyWord = false;
        this.axios
          .get(
            "https://apis.netstart.cn/music/search/suggest?type=mobile&keywords=" +
              n.trim()
          )
          .then((res) => {
            this.suggests = res.data.result.allMatch;
          });
      } else {
        this.suggests = [];
        this.searchSongs = [];
        this.showHotKeyWord = true;
      }
    },
  },
  // components: { PlayMusicCrad },
  created() {
    this.axios
      .get("https://apis.netstart.cn/music/search/default")
      .then((res) => {
        this.defultWord = res.data.data.showKeyword;
        this.realkeyword = res.data.data.realkeyword;
      });
    this.axios.get("https://apis.netstart.cn/music/search/hot").then((res) => {
      this.hotSearchs = res.data.result.hots;
    });
    this.historySearchs = JSON.parse(
      window.localStorage.getItem("serach-history")
    );
  },
};
</script>

