<template>
  <div class="page">
    <div class="mask"  :style="{ backgroundImage:'url(' + playSong.picUrl||playSong.al.picUrl||picUrl +')' }"></div>
    <PageHead :playSong="playSong" :isPlaying="isPlaying" />
      <KeepAlive>
    <PagePic  v-if="isLyric" @click.native="isLyric = !isLyric" :playSong="playSong" :isPlaying="isPlaying" />
    <PlayLyric
        v-else
        @click.native="isLyric = true"
        :currentPlaySongId="playSong.id"
        :currentTime="currentTime"
        :isPlaying="isPlaying"
    />
      </KeepAlive>

    <PageBottom :Songs="Songs"  :playSong="playSong" :currentPlaySongId="currentPlaySongId" @update:time="$emit('update:time',$event)" :duration="duration" :currentTime="currentTime" :isPlaying="isPlaying"  @pause="$emit('pause')" @playing="$emit('playing')"/>
  </div>
</template>
<script>
import PageHead from './PageHead'
import PagePic from './PagePic'
import PageBottom from './PageBottom'
import PlayLyric from './PlayLyric'
export default {
  name: "PlayPage",
  components:{
    PageHead,
    PagePic,
    PageBottom,
    PlayLyric
  },
  props:{
    playSong:Object,
    isPlaying:Boolean,
    currentTime:Number,
    duration:Number,
    Songs:Object
  },
  data(){
    return{
      picUrl: 'https://p1.music.126.net/oZYWmV_y-7z_4y_pfap_TQ==/109951166423703455.jpg',
      isLyric:false,
      currentPlaySongId:this.playSong.id
    }
  }
}
</script>

<style lang="less" scoped>
  .page{
    padding: 10px;
    width: 100vw;
    height: 100vh;
    background-color:#2c3e50 ;
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    .mask{
      width: 100vw;
      height: 100vh;
      position: absolute;
      top: 0;
      left: 0;
      background-size: cover;
      background-position: center;
      filter: blur(20px) brightness(0.7);
      transform: scale(1.3);
      overflow: hidden;
      z-index: -1;
    }
  }
</style>