<template>
  <div class="bottom">
    <div class="box">
      <input type="range" min="0" :max="duration" @input="isInput = false" v-model='value' @change="change" >
      <div class="bar">
        <span :style="{width: (value / duration) * 100 + '%' }"></span>
      </div>
      <div class="control">
        <div class="mode"><img src="../assets/ero.png" alt=""></div>
        <div class="previous" @click="previous"><img src="../assets/d_p.png" alt=""></div>
        <div class="play" @click="playing">
          <img  v-if="!isPlaying" src="../assets/d_o.png" alt="">
          <img  v-else src="../assets/d_n.png" alt="">
        </div>
        <div @click="next" class="next"><img src="../assets/d_m.png" alt=""></div>
        <div class="list" @click.stop="$parent.$parent.isList = true"><img src="../assets/esc.png" alt=""></div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "PageBottom",
  props:{
    isPlaying:Boolean,
    currentTime:Number,
    duration:Number,
    playSong:Object,
  },
  data(){
    return{
      value: 0,
      isInput:true,
      currentPlaySongId:this.playSong.id
    }
  },
  methods:{
    next(){
      this.$parent.$emit('jia' )
    },
    previous(){
      this.$parent.$emit('jian' )
    },

    playing(){
      this.$emit('playing')
    },
    change(){
      this.$emit('update:time',this.value)
      // var index = this.currentParsedLyric.findIndex
    }
  },
  watch:{
    currentTime(){
      if(this.isInput){
        this.value = this.currentTime
      }
    }
  }
}
</script>

<style lang="less" scoped>
  .bottom{
    height: 25vh;
    //background-color: #f7931e;
    .box{
      height: 100%;
      display: flex;
      flex-direction: column;
      justify-content: flex-end;
      align-items: center;
      input{
        width: 75%;
        opacity: 0;
      }
      .bar {
        width: 75%;
        height: 2px;
        background: rgba(255, 255, 255, 0.1);
        span {

          display: block;
          height: 100%;
          width: 0;
          background: rgba(255, 255, 255, 0.1);
          align-items: center;
          position: relative;

          &::after {
            content: "";
            display: block;
            width: 8px;
            height: 8px;
            background: white;
            // transform: translate(5px, -4px);
            position: absolute;
            right: -5px;
            top: -3px;
            border-radius: 50%;
          }
        }
      }
      .control{
        margin: 8px 0;
        width: 80%;
        display: flex;
        justify-content: space-between;
        align-items: center;

        div{
          width: 40px;
          height: 40px;
          //background-color: #ebf2f1;
          &:nth-child(3){
            width: 60px;
            height: 60px;
          }
        }
      }
    }
  }
</style>